import React from "react"
import { useEffect, useState } from "react"
import axios from "axios"
import { useMemo } from "react"
const ProductsContext = React.createContext()
export const ProductsProvider = (props) => {
    const [Products,setProducts] = useState([])
    const [Categories,setCategories] = useState([])
    const url = "http://192.168.0.3:1337/"

    const fetchProducts =async ()=>{
        let rawData = await axios.get(url+"productos")
        setProducts(rawData.data)
    }

    const fetchCategories =async ()=>{
        let rawData = await axios.get(url+"categorias")
        setCategories(rawData.data)
    }

    useEffect(()=>{
        fetchProducts()
        fetchCategories()
    },[])

    const valueContext = useMemo(()=>{
        return({
            Products,
            Categories
        })
    },[Products,Categories])

    return <ProductsContext.Provider value={valueContext} {...props} />
}

export const useProducts = ()=>{
    const context = React.useContext(ProductsContext)

    if(!context)
    throw new Error("Context undefined")

    return context
}