import { Banner, Title, CategoryContainer, Category, CategoryIcon, CategoryName } from "./Banner-style"
import { useProducts } from "../../../../contexts/Products"
const Index = () => {
    const { Categories } = useProducts()
    const renderCategory =(cat)=>{
       return (
        <Category>
            <CategoryIcon className={cat.Icono ? cat.Icono :"fas fa-toolbox"} href=""/>
            <CategoryName children={cat.Nombre}/>
        </Category>
        )
    }
    return(<>
    <Banner>
    <Title children={"Categorias"}/>
    <CategoryContainer>
        {Categories && Categories.map((cat,i)=>renderCategory(cat))}
    </CategoryContainer>
    </Banner>
    </>)
}

export default Index